import React from 'react';
import styled from 'styled-components';
import SmoothScroll from 'smooth-scroll';
import SectionHome from '../components/HomePageComponents/SectionHome';
import SectionProjects from '../components/HomePageComponents/SectionProjects';
import SectionAboutMe from '../components/HomePageComponents/SectionAboutMe';
import SectionContact from '../components/HomePageComponents/SectionContact';

//yarn add three react-three-fiber
//yarn lerp
//yarn add smooth-scroll
//yarn add threejs-meshline

const HomePage = () => {
  const scroll = new SmoothScroll ('a[href*="#"]', {
    speed: 1000,
  });
  console.log (scroll);
  const width = window.innerWidth;
  return (
    <HomeWrapper>
      <div className="container-fluid wrapper">
        <div className="col">
          <SectionHome />
          <SectionProjects />
          <SectionAboutMe />
          <SectionContact />
        </div>
      </div>
    </HomeWrapper>
  );
};

const HomeWrapper = styled.div`
  background: #282c34;
  .wrapper{
    display: flex;
    justify-content: center;
  width: 87%;
  }
`;

export default HomePage;
