import React from 'react';
import styled from 'styled-components';

const Projects = () => {
  return (
    <ProjectsWrapper>
      <div className="div-principal">
        <div className="div-ui">
          Hello from Projects Page
          <button type="button" className="btn btn-success"></button>
        </div>
        <div className="div-back"></div>
      </div>
    </ProjectsWrapper>
  );
};

export default Projects;

const ProjectsWrapper = styled.div`
  .div-principal {
    display: flex;
    justify-content: center;
    position: relative;
  }
  .div-back {
    position: absolute;
    background: yellow;
    width: 60vh;
    height: 60vh;
    z-index: -1;
  }
  .div-ui {
    color: PINK;
    font-weight: 500px;
    font-size: 50px;
  }
`;
